<template>
  <div class="not-found">
    <div class="content">
      <h1>404</h1>
      <h2>页面不存在</h2>
      <p>抱歉，您访问的页面不存在或已被删除</p>
      <el-button type="primary" @click="goHome">返回首页</el-button>
    </div>
  </div>
</template>

<script>
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'

export default {
  name: 'NotFound',
  setup() {
    const router = useRouter()
    const store = useStore()

    const goHome = () => {
      const role = store.getters.userRole
      if (role === 0) {
        router.push('/student/home')
      } else if (role === 1) {
        router.push('/teacher/home')
      } else if (role === 2) {
        router.push('/admin/home')
      } else {
        router.push('/login')
      }
    }

    return {
      goHome
    }
  }
}
</script>

<style scoped>
.not-found {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f5f7fa;
}

.content {
  text-align: center;
}

h1 {
  font-size: 120px;
  margin: 0;
  color: #409eff;
}

h2 {
  font-size: 30px;
  margin: 0;
  color: #303133;
}

p {
  margin: 20px 0 30px;
  color: #606266;
}
</style> 